.bio__text{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: justify;
    color: $lightGrey;
    font-size: 9px;
    font-weight: 500;
    text-decoration: none;
    margin-bottom: 20px;

    @media (max-width: 374px) {
        font-size: 8px;
    }

    @media (min-width: 375px) and (max-width: 767px){
        font-size: 9px;
    }

    @media (min-width: 768px) and (max-width: 1024px){
        font-size: 20px;
    }

    @media (min-width: 1024px) {
        font-size: 25px;
    }
}
